
* {
	box-sizing: border-box;
	transition: all 0.3s ease;
}
.fade-enter-active,
.fade-leave-active {
	transition: opacity .5s;
}
.fade-enter,
.fade-leave-to

/* .fade-leave-active below version 2.1.8 */
	{
	opacity: 0;
}
#app {
	font-family: 'Avenir', Helvetica, Arial, sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	text-align: center;
	color: #2c3e50;
	margin-top: 60px;
}
html,
body {
	margin: 0 auto;
	width: 100%;
	height: 100%;
	overflow: hidden;
	box-sizing: border-box;
}
body{
	background: linear-gradient(to right bottom, rgba(255, 210, 111, 0.6), rgba(54, 119, 255, 0.6));
	/* filter:blur(15px); */
}
.home_audio{
	cursor: pointer;
	color: #FFFFFF !important;
	top: 5%;
	right: 5%;
	z-index: 999;
	position: absolute;
	text-align: center;
	margin: 0 auto;
	height: 35px;
	width: 35px;
	font-size: 18px;
	color: #999999;
	background-color: #52140e;
	border-radius: 50%;
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: space-between;
	animation: audioIn 3s infinite;
	background: radial-gradient(white, green, blue);
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.1), 0 3px 10px 0 rgba(0, 0, 0, 0.1);
}
.home_audio_audio{
	display: none;
}
.home_audio_div{
	box-sizing: border-box;
	text-align: center;
	margin: 0 auto;
	height: 35px;
	width: 35px;
	line-height: 33px;
	vertical-align:top;
	border-radius: 50%;
}
.home_egg_particles{
	width: 100%;
	height: 100%;
}
.home_top_muen {
	cursor: pointer;
	color: #000000 !important;
	top: 0px;
	z-index: 9;
	position: relative;
	text-align: center;
	height: 50px;
	width: 50px;
	line-height: 50px;
	font-size: 2em;
	color: #999999;
	border-radius: 50%;
	display: none;
}
@media (max-width: 1400px) {
.home {
		background-color: #FFFFFF !important;
}
.home_egg_botom_button {
		border: 2px solid #b6c1c7 !important;
}
}
@media (max-width: 600px) {
.home_top_muen {
		display: inherit !important;
}
.home_tracing{
		height: 100% !important;
		border-radius: 0px !important;
		border: 0px solid #b6c1c7 !important;
}
.home {
		top: 0% !important;
		bottom: 0% !important;
		left: 0% !important;
		right: 0% !important;
		padding-top: 0px !important;
		padding-left: 0px !important;
		padding-right: 0px !important;
		border-radius: 0px !important;
		box-shadow: 0 5px 10px rgba(0, 0, 0, 0) !important;
}
.home_egg {
		height: 100% !important;
		border: 0px solid #b6c1c7 !important;
		border-radius: 0px !important;
}
.home_egg_botom {
		display: none !important;
}
}
.home {
	position: fixed;
	box-sizing: border-box;
	top: 10%;
	bottom: 10%;
	left: 15%;
	right: 15%;
	height: auto;
	padding-top: 15px;
	padding-left: 15px;
	padding-right: 15px;
	background-color: #FFFFFF;
	border-radius: 25px;
	box-shadow: 0 5px 10px rgba(0, 0, 0, 0.25);
	overflow-y: hidden;
	z-index: 999;
}
.home_tracing{
	width: 100%;
	height: calc(100% - 50px );
	box-sizing: border-box;
	/* overflow-y: hidden; */
	border-radius: 15px;
}
.home_egg {
	box-sizing: border-box;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	background-color: #FFFFFF !important;
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 15px;
	overflow-y: auto;
	padding: 15px;
}
.home_egg_botom {
	position: relative;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	align-self: flex-end;
}
.home_egg_botom_button {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 40px;
	height: 40px;
	border: 2px solid #b6c1c7;
	border-radius: 50%;
}
.home_egg_botom_button:hover {
	background-color: #A2B0B8;
}
.home_egg_botom_button:hover .home_egg_botom_button_square {
	border: 1px solid #FFFFFF;
	background-color: #FFFFFF;
}
.home_egg_botom_button:active {
	background-color: #ffffff;
}
.home_egg_botom_button:active .home_egg_botom_button_square {
	border: 1px solid #666666;
	background-color: #666666;
}
.home_egg_botom_button_square {
	border-radius: 3px;
	height: 70%;
	width: 70%;
	border: 2px solid #b6c1c7;
	background-color: #b6c1c7;
}
@keyframes audioIn{
from{
		transform: rotate(0deg);
}
to{
		transform: rotate(360deg);
}
}

